import React from 'react'
import './LeftBar.css'
import Description from './Description'
import Info from './Info'
import ListOfAccounts from './ListOfAccounts'
import SideOptions from './SideOptions'
export default function LeftBar(props) {
  return (
    <>
      {props.Profile?<div className="d-none d-md-block"> <Info/></div>:<Description/>}
      {props.Profile ?<div className="d-none d-md-block"> <ListOfAccounts Profile {...props}/> </div>:<SideOptions/>}
      {/* <ListOfAccounts Profile/> */}
    </>
  )
}
