import React, { useContext, useRef, useState } from 'react'
import ChatIcon from '@mui/icons-material/ChatOutlined';
import NotificationsIcon from '@mui/icons-material/NotificationsOutlined';
import Tooltip from '@mui/material/Tooltip';
import SearchIcon from '@mui/icons-material/Search';
import LogoutIcon from '@mui/icons-material/Logout';
import HomeOutlinedIcon from '@mui/icons-material/HomeOutlined';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import Context from '../context/contextapi';
// import FastfoodIcon from '@mui/icons-material/Fastfood';
import './Header.css';


const UserResult=(props)=>{
   return (<>
    <Link to={`/profile/${props._id}`} className='text-decoration-none text-dark'>
    <div className={`userSearchResult w-100 d-flex align-items-center cursor-pointer`} >
      <img src={(props.profileImagescr ? props.profileImagescr : "/images/NoProfile.png")} className='me-2' alt="Profile" />
      <div className="nameAndLastMessageInConversations">
        <h6 className='mb-0 nameInConversation'>{props.username}</h6>
      </div>
    </div>
    </Link>
   </>)
}


export default function Header() {
  const navigate = useNavigate();
  const { dispatch } = useContext(Context);
  const searchUserQuery=useRef('');
  const [searchUserList,setSearchUserList]=useState([]);
  const contextData = useContext(Context);

  const LogoutFunction = async () => {
    try {
      const result = await axios.delete("/api/auth/logout");
      if (result.data === "LogOut Successful") {
        dispatch({ type: "LogOut" });
        navigate('/login', { replace: true });
      }
      else {
        alert("Error occurred");
      }
    } catch (err) {
      console.log(err);
    }
  }

  const getUserResults = (e)=>{
    const fetchData= async ()=>{
      if(e.target.value.trim().length===0)
      setSearchUserList([]);
      else{
        console.log(e.target.value);
        const result = await axios.get(`/api/user/findUsers/${contextData.user.userID}/${e.target.value}`);
        console.log(result);
        if(result.status===200)
        setSearchUserList(result.data);
        else
        setSearchUserList([]);
      }
    }
    fetchData();
  }

  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-light Navbar box-shadow">
        <div className="container-fluid">
          <Link className="navbar-brand ms-3 " to="/"><span className='text-primary logo'>FlavourFusion</span></Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse rounded" id="navbarSupportedContent">
            <ul className="navbar-nav w-100 mb-2 mb-lg-0 d-flex align-item-center justify-content-end ">
            
              <li className="nav-item nav-Item position-relative d-flex align-items-center justify-content-center">
                
                <input
                  type="text"
                  className="searchBarHeader"
                  placeholder="Search"
                  aria-label="Search"
                  aria-describedby="search-addon"
                  ref={searchUserQuery}
                  onChange={getUserResults}
                />
                <div className="position-absolute userSearchResultContainer rounded">
                  {searchUserQuery.current.value && searchUserQuery.current.value.trim().length > 0 && searchUserList.length>0 && searchUserList.map(element=><UserResult {...element} key={element._id} />)}
                </div>
                <SearchIcon className='position-absolute searchIconHeader' />
              </li>

              <Link to='/' className='homeIcon text-decoration-none'>
                <li className="nav-item ">
                  <Tooltip title='Home'>
                    <div className="nav-icon-Item d-flex align-items-center justify-content-center">
                      <a className="nav-Link nav-link position-relative" href="/"><HomeOutlinedIcon className="homeIconInNav actualIconsInNav text-primary" />
                      <span className="position-absolute bg-danger text-center rounded-circle messageCountOnChatIcon">1</span>
                    </a>
                      <span className='d-lg-none text-dark ms-2'>Home</span>
                    </div>
                  </Tooltip>
                </li>
              </Link>

              <Link to='/chat' className='text-decoration-none'>
                <li className="nav-item ">
                  <Tooltip title='Chats'>
                    <div className="nav-icon-Item d-flex align-items-center justify-content-center">
                    <a className="nav-Link nav-link position-relative" href="/">
                      <ChatIcon className=" actualIconsInNav text-primary" />
                      <span className="position-absolute bg-danger text-center rounded-circle messageCountOnChatIcon">1</span>
                    </a>
                    <span className='d-lg-none text-dark ms-2'>Chats</span>
                    </div>
                  </Tooltip>
                </li>
              </Link>
              <li className="nav-item">
                <Tooltip title='Notifications'>
                <div className="nav-icon-Item d-flex align-items-center justify-content-center">
                  <a className="nav-Link nav-link position-relative" href="/"><NotificationsIcon className=" actualIconsInNav text-primary" />
                    <span className="position-absolute bg-danger text-center rounded-circle notifcationCountOnNotificationIcon">1</span>
                  </a>
                <span className='d-lg-none text-dark ms-2'>Notifications</span>
                </div>
                </Tooltip>
              </li>

              <li className="nav-item d-flex align-items-center justify-content-center me-lg-4" onClick={LogoutFunction}>
                <Tooltip title='Logout'>
                  <div className="nav-icon-Item d-flex align-items-center justify-content-center">
                  <LogoutIcon className=" actualIconsInNav text-primary" />
                  <span className='d-lg-none text-dark ms-2'>Logout</span>
                  </div>
                </Tooltip>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  )
}
