import React, { useContext, useState } from 'react'
import './UpdataProfilePage.css'
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import Button from '@mui/material/Button';
import axios from 'axios'
import Context from '../context/contextapi'
import { useNavigate } from 'react-router-dom';

const status = [
    {
        value: 'Single',
        label: 'Single',
    },
    {
        value: 'In a Relationship',
        label: 'In a Relationship',
    },
    {
        value: 'Married',
        label: 'Married',
    },
];

const UpdateProfilePage = () => {
    const contextData = useContext(Context);
    const navigate = useNavigate();
    const [username, setUserName] = useState('');
    const [desc, setDesc] = useState('');
    const [city, setCity] = useState('');
    const [worksAt,setWorksAt]= useState('');
    const [curStatus, setCurStatus] = useState('Single');
    const updateProfile = async () => {
        if(desc.length>100){
            alert("Description cannot be more than 50 characters");
            return;
        }

        const data = {};
        if (username)
            data.username = username;
        if (desc)
            data.description = desc;
        if (city)
            data.city = city;
        if(worksAt)
            data.worksAt= worksAt;
        if (curStatus)
            data.status = curStatus;
        console.log(data);
        const result = await axios.patch(`/api/user/update/${contextData.user.userID}`, { ...data, _id: contextData.user.userID });
        if (result.status === 200) {
            alert('Profile Updated Successfully');
            navigate(`/profile/${contextData.user.userID}`, { replace: true });
        } else {
            alert("Error Occured While Updating the Profile");
        }
    }

    return (
        <>
            <div className="updatePageContainer d-flex align-items-center justify-content-center">
                <div className="updatePageInnerContainer d-flex flex-column align-items-center box-shadow">
                    <h3>Update Profile</h3>
                    <TextField
                        id="outlined-required"
                        label="Username"
                        defaultValue=""
                        className='inputFieldsInUpdate'
                        onChange={(event) => {
                            setUserName(event.target.value);
                        }}
                    />
                    <TextField
                        id="outlined-multiline-flexible"
                        label="Description"
                        multiline
                        maxRows={4}
                        className='inputFieldsInUpdate'
                        onChange={(event) => {
                            setDesc(event.target.value);
                        }}

                    />
                    <TextField
                        id="outlined-required"
                        label="City"
                        defaultValue=""
                        className='inputFieldsInUpdate'
                        onChange={(event) => {
                            setCity(event.target.value);
                        }}
                    />

                    <TextField
                        id="outlined-required"
                        label="Works/Studies At"
                        defaultValue=""
                        className='inputFieldsInUpdate'
                        onChange={(event) => {
                            setWorksAt(event.target.value);
                        }}
                    />

                    <TextField
                        id="outlined-select-currency"
                        select
                        label="Status"
                        defaultValue="Single"
                        helperText="Please select your current status"
                        className='inputFieldsInUpdate'
                        onChange={(event) => {
                            setCurStatus(event.target.value);
                        }}
                    >
                        {status.map((option) => (
                            <MenuItem key={option.value} value={option.value}>
                                {option.label}
                            </MenuItem>
                        ))}
                    </TextField>
                    <Button variant="contained" className='inputFieldsInUpdate pt-2 pb-2' onClick={updateProfile}>Submit</Button>
                    <p className=' fs-6 text-secondary mb-0'>Note:Empty fields will not be updated</p>
                </div>
            </div>
        </>
    )
}

export default UpdateProfilePage;