import React, { useContext, useEffect, useState } from 'react'
import './Info.css'
import EditIcon from '@mui/icons-material/Edit';
import { Link, useParams } from 'react-router-dom';
import axios from 'axios'
import Context from '../context/contextapi';

export default function Info() {
  const contextData = useContext(Context);
  const {id}=useParams();
    const [userData, setUserData] = useState("");
    useEffect(() => {
        const fetchData = async () => {
            const result = await axios.get(`/api/user/${contextData.user.userID}`);
            setUserData(result.data);
        }
        fetchData();
    }, [contextData.user.userID])

  return (
    <>
        <div className="infoWrapper box-shadow mx-auto">
            <h4 className='d-flex align-item-center'><span className='fw-bold mb-3 me-auto d-block'>Your Info</span> {contextData.user.userID===id && <Link to='/update' className=' text-dark' ><EditIcon role='button'/></Link>} </h4>
            <p><span className='fw-bold'>Status </span> {userData.status ? userData.status : "Not Specified"}</p>
            <p><span className='fw-bold'>Lives in </span> {userData.city ? userData.city:"Earth"}</p>
            <p><span className='fw-bold'>Works/Studies at</span> {userData.worksAt?userData.worksAt:"Unknown"}</p>
        </div>
    </>
  )
}
